<template lang="pug">
    v-card
        v-toolbar(flat)
            v-toolbar-title 产量构成曲线
            v-spacer
            v-btn(color="success" @click="update_line_data") 更新曲线
        
        v-divider
        v-row
            v-col(cols="12" lg="2")
            v-col(cols="12" lg="8")
                v-combobox(
                        v-model="title"
                        :items="titles"
                        label="选择字段"
                    )
            v-col(cols="12" lg="2")
        v-divider
        div(ref="prodCompElement" class="prodCompClass")
            VegaChartView(
                :key="chartId"
                :id="chartId"
                :chartId="chartIdStr"
                :chartSource="prodCompData.prodCompData"
                :chartNum="chartNum"
            )
</template>

<script>

import { get } from 'vuex-pathify'
import prodCompData from "./ProdCompData.js";
import * as api from "@/apis/wswebapi"
export default {
  name: "ProdCompLine",
  data: () => ({
        prodCompData: prodCompData,
        chartNum:0,
        title:{text:"产油量",value:"MOIL"},
        titles:[{text:"产油量",value:"MOIL"},{text:"产液量",value:"MLIQUID"},{text:"产水量",value:"MWATER"}],
        chartIdStr:"#ProdCompLine",
        chartId:"ProdCompLine",
  }),
  computed:{
      screenWidth:get("app/screenWidth"),
      blockname:get("peproject/blockname"),
    //   chartId(){
    //       let cid = Math.random().toString(36).slice(-8);
    //       this.setChartId(cid);
    //       return cid 
    //   }
  },
  methods:{
      //   更新曲线
    update_line_data(){
        api.GetProdCompData({blockname:this.blockname,collection:"oilreportmonth",title:this.title.value}).then(res=>{
            if (res.code==200){
                this.prodCompData.prodCompData.datasets.prodCompData=res.data;
                this.chart();
            }else{
                console.log("返回错误",res.code)
                this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true});
            }
        })
    },
      setChartId(){
          let cid = Math.random().toString(36).slice(-8);
          this.chartId=cid;
          this.chartIdStr="#"+cid;
          console.log("ProdCompLine ",this.chartId,this.chartIdStr);
      },
      chart(){
          console.log("宽度:",this.$refs.prodCompElement.clientWidth);
          this.prodCompData.prodCompData.width=this.$refs.prodCompElement.clientWidth-150;
          this.chartNum=this.chartNum+1;
      }
  },


    watch:{
        screenWidth(){
            this.setChartId();
            setTimeout(this.chart,2000);
        }
    },
    components: {
      VegaChartView: () => import('@/views/charts/vegacharts/VegaChartView'),
    },

};
</script>

<style>
.prodCompClass{
    width:100%
}
</style>